* {
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}


.Certificat {
    margin-left: 150px;
    margin-top: 50px;
    margin-right: 150px;
    margin-bottom: 150px;
}


.Certificat .certifcate-title {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 75px;
    font-size: 45px;
    font-weight: 800;
}



.Certificat-main-section {
    display: grid;
    grid-template-columns: 350px 350px 350px;
    justify-content: center;
}


.Certificat-main-section div {
    margin-right: 20px;
    margin-left: 50px;

}


.Certificat-main-section div h2 {
    padding-top: 15px;
    text-align: center;
}


.Certificat-main-section div h6 {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 18px;
}


.Certificat-main-section div a {
    text-decoration: none;
    color: white;
    font-weight: bold;
    background-color: rgb(61, 86, 87);
    margin-left: 100px;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 4px;


}



.Certificat-main-section-div-one {
    background-color: grey;
}

.Certificat-main-section-div-one img {
    height: 150px;
    width: 280px;
    background-color: #5BF1C1;
    padding: 25px;
}



.Certificat-main-section-div-two {
    background-color: grey;
}


.Certificat-main-section-div-two img {
    height: 150px;
    width: 280px;
    background-color: #5BF1C1;
    padding: 25px;
}


.Certificat-main-section-div-three {
    background-color: grey;

}



.Certificat-main-section-div-three img {
    height: 150px;
    width: 280px;
    background-color: #5BF1C1;
    padding: 20px;

}




/* responsible style */






@media (max-width: 1080px) {}





