* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}



.myskill {
    margin-bottom: 0px;
    margin-left: 100px;
    margin-right: 150px ;
    margin-top: 75px;
}

.myskill h4{
    margin-bottom: 20px;
    font-size: 18px;
}

.myskill-title {
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
}


.myskill-title::before {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 180px;
    height: 3px;
    background: black;
    transform: translateX(-50%);
}

.myskill-title::after {
    content: "who i know";
    position: absolute;
    bottom: -5px;
    left: 50%;
    font-size: 20px;
    color: crimson;
    padding: 5px;
    height: 15px;
    background: white;
    transform: translateX(-50%);
}


.myskill-main-section {
    display: grid;
    grid-template-columns: 50% 50%;
}



.myskill-section-one{
    text-align: justify;
    padding: 25px;
}

.myskill .para{
 font-size: 18px;
 word-spacing: 1px;
}


.myskill-section-two{
    width: calc(100%);
    margin: 25px;
    
}

.myskill-section-two .bars{
    margin-bottom: 25px;
    
}


.myskill-section-two .info{
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.myskill-section-two span{
    font-weight: 500;
    font-size: 18px;
}

.myskill-section-two .line{
height: 5px;
width: 100%;
background-color: rgb(215, 239, 248) ;
position: relative;
}

.myskill-section-two .line::before{
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    background:rgb(115, 208, 245);
}



.myskill-section-two .html::before{
    width: 90%;
}


.myskill-section-two .css::before{
    width: 50%;
}



.myskill-section-two .js::before{
    width: 60%;
}


.myskill-section-two .react::before{
    width: 70%;
}



.myskill-section-two .react-native::before{
    width: 35%;
}


